<template>
  <div class="customer-list">
    <el-form :model="form" :inline="true" ref="form" size="small">
      <el-form-item prop="">
        <el-date-picker v-model="form.dateRange" type="daterange" align="right" placeholder="选择日期范围" class="w240"
                        :clearable="false" @change="onRangeChange" size="small">
        </el-date-picker>
      </el-form-item>
      <el-form-item prop="">
        <el-input
          placeholder='操作人'
          v-model='form.searchInput'>
          <i slot='prefix' class='el-input__icon el-icon-search'></i>
        </el-input>
      </el-form-item>
      <el-form-item label="操作类型：">
        <el-select v-model="form.operateSelect" placeholder="请选择">
          <el-option label='WEB' value='1'></el-option>
          <el-option label='iOS' value='2'></el-option>
          <el-option label='Android' value='3'></el-option>
          <el-option label='微信' value='4'></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <el-table :data="tableParams.list" header-align="center" border header-row-class-name="header-center">
      <el-table-column label="客户名称" prop="customer_name"></el-table-column>
      <el-table-column label="联系人" prop="linkman_name"></el-table-column>
      <el-table-column label="所属行业" prop="industry_type_name">
      </el-table-column>
      <el-table-column label="所在地" prop="city_name">
      </el-table-column>
      <el-table-column label="客户级别" prop="customer_level">
        <template slot-scope="scope">
          <span>{{scope.row.customer_level|customerLevel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="负责人" prop="director_name">
      </el-table-column>
      <el-table-column label="创建时间" prop="create_time" width="180">
        <template slot-scope="scope">
          <span>{{scope.row.create_time|dateFormat}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" fixed="right" width="180" class="tc">
        <template slot-scope="scope">
          <el-button type="text" @click="toOpen" size="mini">
            服务开通
          </el-button>
          <el-button type="text" size="mini" @click.native="toNewCustomer(scope.row)">
            编辑
          </el-button>
          <el-button type="text" @click="toDel(scope)" size="mini">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                   :current-page="tableParams.currentPage" :page-sizes="tableParams.pageSizeList"
                   :page-size="tableParams.pageSize" :layout="tableParams.layout" :total="tableParams.totalCount"
                   v-if="tableParams.totalCount" class="mt15">
    </el-pagination>
  </div>
</template>

<script>
  import apis from "@/api";
  import tableMixins from "@/mixins/tableMixin";
  import {
    customerLevel
  } from "@/views/const";
  import {
    fmt,
    removeEmpty
  } from "@/utils/util";
  import {configData, configRule} from './detailConfig'

  export default {
    data() {
      return {
        industryType: [],
        dateRange: "",
        customerLevel,
        customerDetail: configData,
        detailRule: configRule,
        form: {
          dateRange: "",
          operateSelect: "",
          searchInput: ""
        },
        detailVisible: false
      };
    },
    mixins: [tableMixins],
    methods: {
      async _loadList() {
        let vm = this;
        let params = removeEmpty(Object.assign({}, vm.query));
        let result = await apis.customer.getCustomerList(vm.assignQuery(params));
        vm.initialTableData(result.rows, result.total);
      },
      toEdit(row) {
        this.$router.push({
          name: "planEdit",
          params: {
            planId: row.id
          }
        });
      },
      async toDel(scope) {
        let vm = this;
        let check = await apis.customer.checkIsDelCustomer(scope.row.id);

        if (check.result) {
          this.$confirm('此操作将永久删除该客户, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            apis.customer.delCustomerById(scope.row.id).then((data) => {
              vm._loadList()
            })
          });

        } else {
          vm.$message({
            message: '该客户不可删除',
            type: 'warning'
          })
        }
      },
      toOpen() {
      },
      //改变时间范围返回查询时间
      onRangeChange(times) {
        this.query.startTime = times[0] || "";
        this.query.endTime = times[1] || "";
      },
      //导出
      exportExcel() {
      },
      //新建客户
      toNewCustomer(row) {
        this.detailVisible = true;
        this.detailRule = configRule
        if (row && row.id) {
          this.customerDetail = row
        } else {
          this.customerDetail = configData
        }

      },
      // restDetail() {
      //   this.customerDetail = {}
      // }
    }
  };
</script>


