import rule from '@/utils/validate-rule'
export const contactRule={
    linkman_name: [
      rule.max({
        max: 20,
        message: "最多不能超过20个字"
      }),
      rule.min({
        min: 2,
        message: "至少2个字"
      })
    ],
    linkman_contact: [
      {
        validator: rule.phone
      }
    ]
  }
export const contactData={
    linkman_name: "",
    linkman_contact: "",
    linkman_post: "",
    is_primary:0,
  }
export const configRule={
    customer_name: [
      rule.required({
        message: "请输入客户名称"
      }),
      rule.max({
        max: 50,
        message: "最多不能超过50个字"
      }),
      rule.min({
        min: 2,
        message: "至少2个字"
      })
    ],
    social_credit_code: [
      rule.required({
        message: "请输入社会信用代码"
      })
    ],
    industry_type_id: [
      rule.required({
        message: "请选择行业大类"
      })
    ],
    industry_subtype_id: [
      rule.required({
        message: "请选择行业子类"
      })
    ],
    linkmen: [{
      linkman_name: [
        rule.required({
          message: "请输入姓名"
        }),
        rule.max({
          max: 20,
          message: "最多不能超过20个字"
        }),
        rule.min({
          min: 2,
          message: "至少2个字"
        })
      ],
      linkman_contact: [
        {
          validator: rule.phone
        }
      ],
      linkman_post: [
        rule.required({
          message: "请输入联系人职务"
        })
      ]
    }]
  }
  export const configData = {
  customer_name:'1111',
  social_credit_code:'1111',
  customer_level:1,
  industry_type_id:'1111',
  industry_subtype_id:'1111',
  linkmen:[{
    linkman_name: "",
    linkman_contact: "",
    linkman_post: "",
    is_primary:1,
  }],
  province_id:11,
  city_id:1,
  address:'1111',
  website:'1111',
  turnover:11.2,
  staff_num:20,
  primary_business:'1111',
  business_licence_img:'1111',
  attaches:[],
  customer_summary:'1111',
}


