export default {
  data() {
    return {
      tableServer: {},
      tableDataList: [],
      tableSelectionItems: [],
      tableTotalCount: 0,
      tableOption: {
        layout: 'total, sizes, prev, pager, next, jumper',
        pageSizeList: [
          10, 20, 30, 40
        ]
      },
      tableParams: {
        page_size: 10,
        page_num: 1
      }
    }
  },
  mounted() {
    this.initTableServer && this.initTableServer().then(() => {
      this.requestData()
    })
  },
  computed: {
    selectedIds() {
      return this
        .tableParams
        .selectedItems
        .map(item => {
          return item[this.pk]
        })
    }
  },
  methods: {
    requestData() {
      if (this.tableServer.loadTableData && this.tableServer.extraParams.id) {
        this.tableServer.loadTableData(this.paramsHandler()).then(() => {
          this.initialTableData(this.tableServer.dataList())
        })
      }
    },
    paramsHandler() {
      return {...this.tableParams, ...this.tableServer.extraParams, ...this.tableServer.searchParams}
    },
    initialTableData(dataList) {
      this.tableDataList = dataList['data_list']
      this.tableTotalCount = dataList['total_num']
      if (this.tableParams['page_size'] === dataList['page_size'] && this.tableParams['page_num'] === dataList['page_num']) {
      } else {
        this.$message('当前页数不匹配')
        console.log(dataList)
        this.tableParams['page_size'] = dataList['page_size']
        this.tableParams['page_num'] = dataList['page_num']
      }
    },
    handleSizeChange(size) {
      if (size && size !== this.tableParams.page_size) {
        this.tableParams.page_size = size
        this.requestData()
      }
    },
    handleCurrentChange(current) {
      if (current && this.tableParams.page_num !== current) {
        this.tableParams.page_num = current
        this.requestData()
      }
    },
    onSelectionChange(val) {
      this.tableSelectionItems = [...val]
    },
    assignQuery(query) {
      return {
        page_num: this.tableParams.pageNum,
        page_size: this.tableParams.pageSize,
        ...query
      }
    },
    onSearch() {
      this.tableParams.pageNum = 1
      this._loadList()
    }
  }
}


